.hero{
    height: 100vh;
    background: linear-gradient(180deg , #fde1ff, #e1ffea22 60%);
    display: flex;
}

.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 130px;
    line-height: 1.1;
}

.hero-left h2{
    color: #090909;
    font-size: 26px;
    font-weight: 600;
    /* margin-left: 2%; */
    /* margin-top: 19%; */

}

.hero-left p{
    color: #171717;
    font-size: 70px;
    font-weight: 700;
    margin: 3%;
    align-content: flex-start ;
}

.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;
    flex-direction: column;
}

.hero-hand-icon img{
    width: 105px;
}

.hero-latest-button{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 60px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.hero-right img{
    height: 680px;
}